<template>
    <div class="ratingselect">
        <div class="rating-type border-1px">
            <span @click="select($event,2)" class="block positive" :class="{'active': computedType === 2}">
                {{desc.all}}
                <span class="count">{{ratings.length}}</span>
            </span>
            <span @click="select($event,0)" class="block positive" :class="{'active': computedType === 0}">
                {{desc.positive}}
                <span class="count">{{positives.length}}</span>
            </span>
            <span @click="select($event,1)" class="block negative" :class="{'active': computedType === 1}">
                {{desc.negative}}
                <span class="count">{{negatives.length}}</span>
            </span>
        </div>
        <div @click="toggleContent" class="switch" :class="{'on': computedContent}">
            <span class="icon-check_circle"></span>
            <span class="text">只看有内容的评价</span>
        </div>
    </div>
</template>
<script>
const POSITIVE = 0,
    NEGATIVE = 1,
    ALL = 2

export default {
    props: {
        ratings: {
            type: Array,
            default() {
                return []
            }
        },
        selectType: {
            type: Number,
            default: ALL
        },
        onlyContent: {
            type: Boolean,
            default: false
        },
        desc: {
            type: Object,
            default() {
                return {
                    all: "全部",
                    positive: "满意",
                    negative: "不满意"
                }
            }
        }
    },
    data() {
        return {
            computedType: this.selectType,
            computedContent: this.onlyContent
        }
    },
    methods: {
        select($event, type) {
            if (!$event._constructed) {
                return
            }
            this.computedType = type
            this.$emit('ratingType', type)
        },
        toggleContent($event) {
            if (!$event._constructed) {
                return
            }
            this.computedContent = !this.computedContent
            this.$emit('contentToggle', this.computedContent)
        }
    },
    computed: {
        positives() {
            return this.ratings.filter((rating) => {
                return rating.rateType === POSITIVE
            })
        },
        negatives() {
            return this.ratings.filter((rating) => {
                return rating.rateType === NEGATIVE
            })
        },
    }
}
</script>
<style lang="stylus">
@import '../../common/stylus/mixin.styl'

    .ratingselect
        .rating-type
            padding 18px 0
            margin 0 18px
            font-size 0
            border-1px(rgba(7,17,27,0.1))
            .block
                display inline-block
                padding 8px 12px
                border-radius 1px
                margin-right 8px
                color rgb(77,85,93)
                font-size 12px
                line-height 16px
                .count
                    font-size 8px
                &.active
                    color #fff
                &.positive
                    background rgba(0,160,220,0.2)
                    &.active
                        background rgb(0,160,220)
                &.negative
                    background rgba(77,85,93,0.2)
                    &.active
                       background rgb(77,85,93) 
        .switch
            padding 12px 18px
            line-height 24px
            border-bottom 1px solid rgba(7,17,27,0.1)
            color rgb(147,153,159)
            font-size 0
            &.on
                .icon-check_circle
                    color #00c850
            .icon-check_circle
                display inline-block
                font-size 24px
                vertical-align top
                margin-right 4px
            .text
                font-size 12px
</style>



